.modal {
  position: fixed;
  inset: 0; /* inset sets all 4 values (top right bottom left) much like how we set padding, margin etc., */
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  z-index: 999;
  padding: 40px 20px 20px;
}

.modal-content {
  width: 70%;
  height: 70%;
  background-color: #282c34;
  padding: 50px;
  color: #fff;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}

.button-row {
  flex-direction: row;
  width: 100%;
  margin: 5%;
}

.End-button {
  margin-left: 15%;
  margin-right: 15%;
  font-size: calc(10px + 2vmin);
  border-radius: 10px;
}

.Blue-border {
  border-width: 10px;
  border-color: blue;
}
